@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
}
body{
  overflow-x:hidden;
}


.App{
  display: grid;
  grid-template-columns: 250px 1fr;
  height: 100vh;
  width: 100%;
  font-family: Roboto;
}

.app_container{
  display: flex;
  flex-direction: column;
  font-family: Roboto;
  max-width: 250px;  
  padding: 1rem; 
}

.app_container_heading{
  margin: 0rem 0rem 2rem 0px ;
  font-size: 1.2rem;
}

.app_container > div:nth-child(2){
  background-color: #8F8F94;;
  display: flex;
  align-items: center;  
  padding: .7rem 1rem;
  border-radius: 20px;
}
/* .app_container > div:nth-child(3){
  display: flex;
  align-items: flex-end;  
  padding: .7rem;
  margin-top: 20px;
  margin-bottom: 10px;
  font-size: 1.1rem;
} */

.app_container > div:nth-child(3) a{
  text-decoration: none;
  color: black;
  display: flex;
  align-items: flex-end;  
  padding: .7rem;
  margin-top: 20px;
  margin-bottom: 10px;
  font-size: 1.1rem;
  width: min-content;
}


.subcontainer{
  padding-top: .5rem;
  height: 250px;
  background-color: #8F8F94;;
  border-radius: 20px;
}

.subcontainer a{
  display: flex;
  align-items: center;
  text-decoration: none;
  color: black;
  width: min-content;
}
a:hover{
  /* text-decoration: underline !important; */
  color: rgba(255, 0, 0, 0.685) !important;
}

.subcontainer div{
  padding: .7rem;
  display: flex;
  align-items: center;
}
.subcontainer div span{
  margin-left: .2rem;
  font-size: 1.1rem;
}



/* ***************** profile start ***************** */
.Profile{
  width: 100%;
  text-align: center;
    max-width: 1400px;
    min-width: 840px;
}

.Profile_grid_container{
  display: grid;
  background-color: rgb(255, 255, 255);
  margin: 1rem;
  padding: 2rem 2rem 2rem 0px;
  width: 100%;
  /* text-align: center; */
    max-width: 1400px;
    min-width: 840px;
  /* margin-right: 2rem; */
}

.Profile > div > h1{
  font-weight: 500;
  text-align: center;
  margin-top: 1rem;
}


.flex_space_between{

  display: flex;
  justify-content: space-between;
  align-items: center;
  /* border-bottom: 1px solid #fafafa; */
  border-bottom: 1px solid #dddddd;
  padding: 2rem;

}
.flex_space_between > div > EditSharpIcon{
  margin-left: 1rem;
}

.flex_space_between > div > button{
  display: flex;
  align-items: center;
  padding: .5rem .7rem ;
  margin-right: 1.5rem;
}



.flex_space_between div{
  display: flex;
  align-items: center;
}

.flex_space_between > div > span{
  margin-left: 1.5rem;
  text-align: left;
}

.flex_space_between:first-child div:first-child{
  align-items: flex-start;
}

.Profile_grid_container_column_3 div:last-child{
  align-items: flex-end;
}

 

.Overall_experience{
  display: flex;
  flex-direction: column;
  justify-content: flex-start !important;
  align-self: center;

}
.Overall_experience div:last-child{
  font-size: 1.1rem;

}
.Overall_experience div:first-child{
  margin-right: 1.7rem;

}

.Profile_grid_container_column_4 div:last-child{
  display: flex;
  /* background-color: #8F8F94; */
}

.Profile_grid_container_column_4 div div:first-child{
  margin-right: 1.5rem;
  padding-bottom: 2rem;

}



.Profile_grid_container_column_2{
  flex-direction: column;
  padding: 2rem !important;
  border-bottom: 1px solid #dddddd;
}
.Profile_grid_container_column_2 div:last-child > div > div:first-child{
  margin-right: .5rem;
}

/* .Profile_grid_container_column_2 > div:last-child{
  display: flex;
  flex-direction: row;
} */
.Profile_grid_container_column_2 > div{
  display: flex;
  justify-content: space-between;
  padding: 0rem 1rem;
}
.name_heading{
  display: flex;
}
.name_heading > div:last-child{
  display: flex;
  flex-direction: column;
  align-content: space-between;
  margin-left: 1rem;
}
.name_heading > div:last-child div:first-child h2{
  font-weight: 500;
}



.justify_space_between{
  margin-top: 1.8rem;
  /* display: flex; */
  width: 100%;
  flex-direction: row;
  /* justify-content: space-between;  */
  border: none;
  /* max-width: 600px; */
  text-align: right;

}
.justify_space_between div{
  display: flex;
}

.image_profile{
  /* width: 100px; */
  background-color: #cccccc;
  color: white;
  width: min-content;
  border-radius: 50%;
  padding: 1.3rem .2rem;
  height: 65px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.name_heading_description{
  margin-top: 2rem;
  text-align: left;
}
/* ***************** profile end ***************** */





/* ***************** home start ***************** */
.Home_container{
  margin: 2rem 2rem 2rem 0px;
  max-width: 1400px;
}

.Home_container_box_1 h1{
  margin-bottom: 1.5rem;
  font-weight: 500;
}
.Home_container_box_1 {
  background-color: #fbfbfb;
  padding: 1.5rem;
  padding-top: 0px; 
}
.Home_container_box_1 h3{
  font-weight: 400;
}

.four_box_row > div:first-child{
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #999999;
  border-radius: 50%;
  width: 25px;
  height: 25px;
  color: white;
  margin-bottom: .5rem;
}

.four_box_row_container{
  display: flex;


}

.four_box_row{
  color: #999999;
  width: 100%;
  min-width: 100px;
  max-width: 180px;
  border-radius: 10px;
  background-color: #ffffff;
  padding: 1rem;
  margin: 1rem;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.247);
}
.four_box_row:first-child{
  margin-left: 0rem;
}


.Home_container_box_2{
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2,  minmax( 200px , 1fr) );
  box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.2);
  margin: 1rem 0rem 0rem 0;

}


.Home_container_box_2_left_side{
  display: flex;
  justify-content: center;
  align-items: center;
}
.Home_container_box_2_left_side img{
  width: 100%;
  height: auto;
  max-height: 450px;
  max-width: 500px;
}

.Home_container_box_2_right_side{
  display: flex;
  flex-direction: column;
  width: 100%;
  padding-right:2rem;
}

.Home_container_box_2_points{
  display: grid;
  grid-template-columns: 35px 1fr;
  margin-bottom: 1.5rem;


}

.number_points{
  background-color: #999999;
  border-radius: 50%;
  width: 25px;
  height: 25px;
  font-size: medium;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  /* margin-right: 1.5rem; */

}

.Home_container_box_2_right_side > h2{
  margin-bottom: 2rem;
  font-weight: 500;
  
}
/* ***************** home end ***************** */